html {
    margin: 0;
    width: 100%;
    height: 100%;
    min-height: 100%;
    overflow: hidden;
}
body {
    background-color: #04b4e0;
    margin: 0;
    width: 100%;
    height: 100%;
    min-height: 100%;
    font-size: 15px !important;
    font-family: poppins, Helvetica, sans-serif;
    line-height: 1.65em !important;
    overflow: hidden;
    color: #d5d5d5 !important;
    background-repeat: no-repeat;
    background-position: center;
    background-position: 50% 50%;
    background-size: cover;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    color: #eee;
    font-family: poppins, Helvetica, sans-serif;
    font-weight: 600 !important;
}
h1 {
    font-size: 32px;
}
h2 {
    font-size: 27px;
}
h3 {
    font-size: 21px;
}
h4 {
    font-size: 18px !important;
}
h5 {
    font-size: 16px;
}
h6 {
    font-size: 14px;
}
a {
    color: #09c !important;
    text-decoration: none !important;
    transition: all 0.3s ease-in-out;
}
p {
    /* margin-bottom: 10px; It was before. The place where the important was used before was without the important. */
    margin-bottom: 10px !important;
}
.clearfix:before,
.clearfix:after {
    content: ' ';
    display: table;
}
.clearfix:after {
    clear: both;
}
.page {
    display: block;
    width: 100%;
    height: 100%;
    min-height: 100%;
    padding: 0 100px;
    overflow: hidden;
}
.page-content {
    position: relative;
    width: 100%;
    max-width: 1280px;
    min-height: 80vh;
    margin: 10vh auto;
    padding: 0;
    background-color: #444;
    box-shadow: 0 0 100px -5px rgba(0, 0, 0, 0.25);
    border-radius: 35px;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
.btn-primary,
.btn-secondary,
button,
input[type='button'],
input[type='submit'],
.wp-block-button .wp-block-button__link {
    display: inline-block !important;
    position: relative !important;
    padding: 0.8em 2.1em !important;
    margin-bottom: 0.75em !important;
    margin-right: 0.25em;
    font-size: 1em !important;
    line-height: 1.2 !important;
    border: 0;
    outline: 0 !important;
    border: 2px solid #04b4e0 !important;
    color: #f5f5f5;
    text-shadow: none !important;
    background-color: transparent !important;
    border-radius: 30px !important;
    font-family: poppins, Helvetica, sans-serif !important;
    transition: all 0.3s ease-in-out !important;
    box-shadow: 0 10px 10px -8px rgba(0, 0, 0, 0.3) !important;
}
.btn-primary:last-child,
.btn-secondary:last-child,
button:last-child,
input[type='button']:last-child,
input[type='submit']:last-child,
.wp-block-button .wp-block-button__link:last-child {
    margin-right: 0;
}
.btn-primary:hover,
.btn-primary:focus,
button:hover,
button:focus,
input[type='button']:hover,
input[type='button']:focus,
input[type='submit']:hover,
input[type='submit']:focus {
    background-color: #04b4e0;
    color: #fff;
    border: 2px solid #04b4e0;
}
.btn-secondary {
    border-color: #d5d5d5;
    background-color: transparent;
    box-shadow: 0 10px 10px -8px rgba(0, 0, 0, 0.3);
    color: #f5f5f5;
}
.btn-secondary:hover,
.btn-secondary:focus {
    color: #333;
    border-color: #d5d5d5;
    background-color: #d5d5d5;
}
.animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}
.no-js .preloader,
.no-js .preloader-portfolio {
    display: none;
}
.preloader,
.preloader-portfolio {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: #222;
}
.preloader-animation {
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    background-color: inherit;
    color: inherit;
    opacity: 1;
    transition: opacity 0.3s;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
}
.preloader-spinner {
    width: 52px;
    height: 52px;
    margin: 100px auto;
    background-color: #04b4e0;
    border-radius: 100%;
    -webkit-animation: sk-scaleout 1s infinite ease-in-out;
    animation: sk-scaleout 1s infinite ease-in-out;
}
@-webkit-keyframes sk-scaleout {
    0% {
        -webkit-transform: scale(0);
    }
    100% {
        -webkit-transform: scale(1.1);
        opacity: 0;
    }
}
@keyframes sk-scaleout {
    0% {
        -webkit-transform: scale(0);
        transform: scale(0);
    }
    100% {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
        opacity: 0;
    }
}
